"use client";

import { useState, useEffect } from "react";
import { Calendar as CalendarComponent } from "@/components/ui/calendar";
import { Card } from "@/components/ui/card";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";

const academicEvents = [
  { date: "2025-02-10", title: "Mula Persekolahan 2025" },
  { date: "2025-04-28", title: "Ujian 1 (Minggu 1)" },
  { date: "2025-04-29", title: "Ujian 1 (Minggu 1)" },
  { date: "2025-04-30", title: "Ujian 1 (Minggu 1)" },
  { date: "2025-05-01", title: "Ujian 1 (Minggu 1)" },
  { date: "2025-05-02", title: "Ujian 1 (Minggu 1)" },
  { date: "2025-06-10", title: "Smart JAWI" },
  { date: "2025-12-01", title: "UPKK" },
];

const publicHolidays = [
  { date: "2025-01-01", title: "Tahun Baru" },
  { date: "2025-01-25", title: "Tahun Baru Cina" },
  { date: "2025-05-01", title: "Hari Pekerja" },
  { date: "2025-05-13", title: "Hari Raya Aidilfitri" },
  { date: "2025-05-14", title: "Hari Raya Aidilfitri" },
  { date: "2025-08-31", title: "Hari Kebangsaan" },
];

export default function Calendar() {
  const [date, setDate] = useState<Date | undefined>(new Date());
  const [countdown, setCountdown] = useState("");

  // Function to calculate countdown to Smart JAWI
  const calculateCountdown = () => {
    const smartJawiDate = new Date("2025-06-10");
    const now = new Date();
    const difference = smartJawiDate.getTime() - now.getTime();

    if (difference <= 0) {
      setCountdown("Event telah berlalu");
      return;
    }

    const days = Math.floor(difference / (1000 * 60 * 60 * 24));
    const hours = Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    const minutes = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((difference % (1000 * 60)) / 1000);

    setCountdown(`${days} hari ${hours} jam ${minutes} minit ${seconds} saat`);
  };

  useEffect(() => {
    const timer = setInterval(calculateCountdown, 1000);
    return () => clearInterval(timer);
  }, []);

  // Function to check if a date has an event
  const getEventsForDate = (date: Date) => {
    const dateStr = date.toISOString().split('T')[0];
    const academic = academicEvents.find(event => event.date === dateStr);
    const holiday = publicHolidays.find(event => event.date === dateStr);
    return { academic, holiday };
  };

  return (
    <div className="space-y-6 p-4">
      <Card className="p-4 bg-orange-50">
        <h3 className="text-lg font-semibold text-orange-600">
          Countdown ke Smart JAWI
        </h3>
        <p className="text-2xl font-bold mt-2">{countdown}</p>
      </Card>

      <Tabs defaultValue="calendar">
        <TabsList>
          <TabsTrigger value="calendar">Kalendar</TabsTrigger>
          <TabsTrigger value="events">Senarai Aktiviti</TabsTrigger>
        </TabsList>

        <TabsContent value="calendar">
          <Card className="p-4">
            <CalendarComponent
              mode="single"
              selected={date}
              onSelect={setDate}
              className="rounded-md border"
              modifiers={{
                academic: (date) => 
                  academicEvents.some(event => event.date === date.toISOString().split('T')[0]),
                holiday: (date) =>
                  publicHolidays.some(event => event.date === date.toISOString().split('T')[0]),
              }}
              modifiersStyles={{
                academic: { backgroundColor: '#FFA500', color: 'white' },
                holiday: { backgroundColor: '#98FF98', color: 'black' },
              }}
            />
            {date && (
              <div className="mt-4">
                {getEventsForDate(date).academic && (
                  <p className="text-orange-600">
                    Aktiviti: {getEventsForDate(date).academic?.title}
                  </p>
                )}
                {getEventsForDate(date).holiday && (
                  <p className="text-green-600">
                    Cuti: {getEventsForDate(date).holiday?.title}
                  </p>
                )}
              </div>
            )}
          </Card>
        </TabsContent>

        <TabsContent value="events">
          <Card className="p-4">
            <div className="space-y-4">
              <div>
                <h4 className="font-semibold text-orange-600 mb-2">Aktiviti Akademik</h4>
                <ul className="space-y-2">
                  {academicEvents.map((event, index) => (
                    <li key={index} className="flex justify-between">
                      <span>{event.title}</span>
                      <span className="text-gray-500">{event.date}</span>
                    </li>
                  ))}
                </ul>
              </div>
              <div>
                <h4 className="font-semibold text-green-600 mb-2">Cuti Umum</h4>
                <ul className="space-y-2">
                  {publicHolidays.map((event, index) => (
                    <li key={index} className="flex justify-between">
                      <span>{event.title}</span>
                      <span className="text-gray-500">{event.date}</span>
                    </li>
                  ))}
                </ul>
              </div>
            </div>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  );
}
