"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";

const initialTeachers = [
  "Puan Aida Laila Aswati",
  "Puan Nur Ainun Shahadah",
  "Puan Rosamanina",
  "Encik Muhamad Mulhim",
  "Puan Nur Dalila",
  "Puan Nur Asmalina",
  "Encik Wan Muhammed Sodiqin",
  "Encik Amirul Hishamuddin",
  "Encik Muhammad Shahirul Hamzi",
  "Encik Ahmad Taqiyuddin"
];

export default function TeacherList() {
  const [teachers, setTeachers] = useState(initialTeachers);
  const [editingIndex, setEditingIndex] = useState<number | null>(null);
  const [editValue, setEditValue] = useState("");

  const handleEdit = (index: number) => {
    setEditingIndex(index);
    setEditValue(teachers[index]);
  };

  const handleSave = (index: number) => {
    if (editValue.trim()) {
      const newTeachers = [...teachers];
      newTeachers[index] = editValue;
      setTeachers(newTeachers);
      setEditingIndex(null);
    }
  };

  const handleCancel = () => {
    setEditingIndex(null);
    setEditValue("");
  };

  return (
    <div className="p-4">
      <Table>
        <TableHeader>
          <TableRow>
            <TableHead className="w-16">No.</TableHead>
            <TableHead>Nama Guru</TableHead>
            <TableHead className="w-32">Tindakan</TableHead>
          </TableRow>
        </TableHeader>
        <TableBody>
          {teachers.map((teacher, index) => (
            <TableRow key={index}>
              <TableCell>{index + 1}</TableCell>
              <TableCell>
                {editingIndex === index ? (
                  <Input
                    value={editValue}
                    onChange={(e) => setEditValue(e.target.value)}
                    className="w-full"
                  />
                ) : (
                  teacher
                )}
              </TableCell>
              <TableCell>
                {editingIndex === index ? (
                  <div className="flex gap-2">
                    <Button 
                      size="sm" 
                      onClick={() => handleSave(index)}
                      className="bg-orange-500 hover:bg-orange-600"
                    >
                      Simpan
                    </Button>
                    <Button 
                      size="sm" 
                      variant="outline" 
                      onClick={handleCancel}
                    >
                      Batal
                    </Button>
                  </div>
                ) : (
                  <Button 
                    size="sm"
                    variant="outline"
                    onClick={() => handleEdit(index)}
                  >
                    Edit
                  </Button>
                )}
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </div>
  );
}
