"use client";

import { useState } from "react";
import Image from "next/image";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";

export default function EditableLogo() {
  const [logoUrl, setLogoUrl] = useState("/school-logo.svg");
  const [newLogoUrl, setNewLogoUrl] = useState("");
  const [isOpen, setIsOpen] = useState(false);

  const handleUpdateLogo = () => {
    if (newLogoUrl) {
      setLogoUrl(newLogoUrl);
      setIsOpen(false);
      setNewLogoUrl("");
    }
  };

  return (
    <div className="relative inline-block">
      <div className="relative group cursor-pointer">
        <Image
          src={logoUrl}
          alt="SRAR Solatiah Logo"
          width={80}
          height={80}
          className="rounded-full bg-white p-1 transition-opacity group-hover:opacity-80"
          priority
        />
        <Dialog open={isOpen} onOpenChange={setIsOpen}>
          <DialogTrigger asChild>
            <div className="absolute inset-0 flex items-center justify-center bg-black/0 group-hover:bg-black/20 rounded-full transition-all">
              <Button
                variant="secondary"
                size="sm"
                className="opacity-0 group-hover:opacity-100 transition-opacity"
              >
                Tukar Logo
              </Button>
            </div>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Kemaskini Logo Sekolah</DialogTitle>
            </DialogHeader>
            <div className="space-y-4 p-4">
              <div className="flex items-center space-x-4">
                <Image
                  src={logoUrl}
                  alt="Current Logo"
                  width={60}
                  height={60}
                  className="rounded-full bg-white p-1"
                />
                <div className="flex-1">
                  <label className="text-sm font-medium block mb-2">URL Logo Baru:</label>
                  <Input
                    type="text"
                    placeholder="Masukkan URL logo..."
                    value={newLogoUrl}
                    onChange={(e) => setNewLogoUrl(e.target.value)}
                  />
                  <p className="text-xs text-gray-500 mt-1">
                    Masukkan URL imej untuk menukar logo sekolah
                  </p>
                </div>
              </div>
              <div className="flex justify-end space-x-2 mt-4">
                <Button
                  variant="outline"
                  onClick={() => {
                    setIsOpen(false);
                    setNewLogoUrl("");
                  }}
                >
                  Batal
                </Button>
                <Button 
                  onClick={handleUpdateLogo}
                  className="bg-orange-500 hover:bg-orange-600"
                >
                  Simpan
                </Button>
              </div>
            </div>
          </DialogContent>
        </Dialog>
      </div>
    </div>
  );
}
